
.card-img{
    width: 280px;
    height: 260px;
    object-fit: cover;
  }
  .card-img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .card{
    color: #444444;
    margin: 10px 10px;
    min-height: 300px;
    width: 280px;
    height: 500px;
    border-radius: 4px;
    box-shadow: 0px 0px 4px rgba(0,0,0,0.50);
    background-color: #fff;
    overflow: hidden;  
    transition: all 0.20s ease-in;
    cursor: pointer;
    /* text-align: center; */
  }
  
  .card:hover{
    box-shadow: 0px 4px 12px 2px rgba(0,0,0,0.25);
    transform: translateY(-6px);
  }
  
